


























import uCharts from '@/js_sdk/u-charts/components/u-charts/u-charts.js';
import service from '../../service.js';
import sharebtn from '../../components/shareBox.vue';
var _self;
var canvaColumn=null;
export default {
	data() {
		return {
			address:{
				addressurl:"",
				addressinfo:"100",
			},
			cWidth:'',
			cHeight:'',
			pixelRatio:1,
			serverData:'',
			serverList:[
				
			],
			id:''
		}
	},
	components:{
		sharebtn
	},
	onLoad(option) {
		_self = this;











		this.cWidth=uni.upx2px(750);
		this.cHeight=uni.upx2px(500);
		if(option.id){
			this.id = option.id;
			this.getServerData();
		}
		
	},
	methods: {
		getServerData(){				
			uni.request({
				url: service.BASEURL+"/home/share/export_data",
				method:"POST",
				data:{
					id:this.id
				},
				success: function(res) {
					_self.address = res.data.data.address;
					// // console.log(res.data.data)
					//
					_self.serverData=res.data.data;
					_self.updateData(res.data.data.list);
					
					// let Column={categories:[],series:[]};
					// push
					// Column.categories=res.data.data.Column.categories;
					// series
					// Column.series=res.data.data.Column.series;
					// _self.showColumn("canvasColumn",Column);
				},
				fail: () => {
					console.log("");
				},
			});
		},
		updateData(list){
			let ServerList = [];
			if(list){
				list.forEach(item=>{
					let nitem = {}
					nitem.project = item.project
					let Column={categories:[],series:[]};
					if(item.num>0){
						// Column.categories = Column.categories.concat(['','','TVOC','','']);
						for(let i = 0; i<item.num;i++){
							Column.categories.push(`${i+1}`);
						}
					}
					if(item.data && item.data.length>0){
						Column.series.push({
							name:'',
							data:item.data.map(m=>m.cho2)
						})
						Column.series.push({
							name:'',
							data:item.data.map(m=>m.c6h6)
						})
						Column.series.push({
							name:'TVOC',
							data:item.data.map(m=>m.tvoc)
						})
						// Column.series.push({
						// 	name:'',
						// 	data:item.data.map(m=>m.humidity)
						// })
						// Column.series.push({
						// 	name:'',
						// 	data:item.data.map(m=>m.temperature)
						// })
					}
					nitem.Column = Column;
					ServerList.push(nitem)						
				})
			}
			if(ServerList.length>0){
				for (let i = 0; i < ServerList.length; i++) {
					this.showColumn(`canvasColumn_${i}`,ServerList[i].Column);
				}
				this.serverList = ServerList;
			}
		},
		showColumn(canvasId,chartData){
			canvaColumn =new uCharts({
				$this:_self,
				canvasId: canvasId,
				type: 'column',
				legend:true,
				fontSize:11,
				background:'#FFFFFF',
				pixelRatio:_self.pixelRatio,
				animation: true,
				categories: chartData.categories,
				series: chartData.series,
				xAxis: {
					disableGrid:true,
				},
				yAxis: {
				},
				dataLabel: true,
				width: _self.cWidth*_self.pixelRatio,
				height: _self.cHeight*_self.pixelRatio,
				extra: {
					column: {
					  width: _self.cWidth*_self.pixelRatio*0.45/chartData.categories.length
					}
				  }
			});
		}
	}
}
